const app = getApp()
// 获取公共域名地址
import serverUrl from '@/common/serveUrl.js';
export default{
	data(){
		return{
			getInfoFlag: true,
			encryptedData: '',
			nickName: '',
			avatarUrl: '',
			mobilePhone: '',
			headImgUrl: '',
			scene:null,
			globalData: {}
		}
	},
	onLoad(query) {
		this.globalData = app.globalData
		var userId = query.userId
	},
	methods:{
		getUserInfo(e) {
			var that = this
			const url = serverUrl.serverUrl;
		    if (e.detail.errMsg == 'getUserInfo:ok') {
				this.ickName = e.detail.userInfo.nickName,
				this.avatarUrl = e.detail.userInfo.avatarUrl,
				uni.request({
					url: url + '/app/user/bindwx',
					method: 'GET',
					header: {
						'Content-Type': 'application/json'
					},
					data: JSON.stringify({
						// openid: app.globalData.openid,
						// mobilePhone: this.mobilePhone,
						userId: this.userId,
						nickName: this.nickName,
						headImgUrl: this.avatarUrl,
						// joinRecomId:this.data.scene?this.data.scene:null
					}),
					success: function(res) {
						console.log(res)
						app.globalData.userInfo=res.data.data;
						uni.switchTab({
							url: '/pages/index/index',
						})
		            }
		          })
		        }
		  },
	}
}
